___CSS_LOADER_EXPORT___.push([module.id, `.content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.content form {
  width: 700px;
  padding: 4rem;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3), 0 1px 3px 1px rgba(60, 64, 67, 0.15);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.content form h2 {
  width: 100%;
}
.content form .form-group {
  width: 100%;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.content form .form-group label {
  font-size: 1.8rem;
  color: #2c3e50;
  font-weight: 700;
  margin-bottom: 1rem;
}
.content form .form-group input, .content form .form-group textarea, .content form .form-group select {
  width: 100%;
  border: 1px solid #ecf0f1;
  outline: none;
  font-size: 1.8rem;
  border-radius: 3px;
}
.content form .form-group input:focus, .content form .form-group textarea:focus, .content form .form-group select:focus {
  border-color: #2ecc71;
}
.content form .form-group input {
  padding: 5px 10px;
}
.content form .form-group textarea {
  min-height: 20rem;
  padding: 10px;
}
.content form .form-group select {
  padding: 10px 15px;
}`, "",{"version":3,"sources":["webpack://./src/form/form.scss","webpack://./src/assets/styles/_mixins.scss","webpack://./src/assets/styles/_variables.scss"],"names":[],"mappings":"AAGA;ECFI,aAAA;EACA,mBAFoB;EAGpB,uBAHmC;EAInC,mBAJmD;ADMvD;AACI;EACI,YAAA;EACA,aAAA;EACA,mFEDK;EFEL,kBAAA;ECVJ,aAAA;EACA,sBDWkB;ECVlB,uBAHmC;EAInC,uBDSkC;AAGtC;AADQ;EACI,WAAA;AAGZ;AAAQ;EACI,WAAA;EACA,mBAAA;ECpBR,aAAA;EACA,sBDqBsB;ECpBtB,uBAHmC;EAInC,uBDmBsC;AAI1C;AAFY;EACI,iBAAA;EACA,cEvBD;EFwBC,gBAAA;EACA,mBAAA;AAIhB;AADY;EACI,WAAA;EACA,yBAAA;EACA,aAAA;EACA,iBAAA;EACA,kBAAA;AAGhB;AAFgB;EACI,qBErCJ;AFyChB;AAAY;EACI,iBAAA;AAEhB;AACY;EACI,iBAAA;EACA,aAAA;AAChB;AAEY;EACI,kBAAA;AAAhB","sourcesContent":["@import '../assets/styles/variables';\r\n@import '../assets/styles/mixins';\r\n\r\n.content {\r\n\r\n    @include flex;\r\n\r\n    form {\r\n        width: 700px;\r\n        padding: 4rem;\r\n        box-shadow: $box-shadow;\r\n        border-radius: 5px;\r\n\r\n        @include flex(column, $align: flex-start);\r\n\r\n        h2 {\r\n            width: 100%;\r\n        }\r\n\r\n        .form-group {\r\n            width: 100%;\r\n            margin-bottom: 2rem;\r\n\r\n            @include flex(column, $align: flex-start);\r\n\r\n            label {\r\n                font-size: 1.8rem;\r\n                color: $accent-color;\r\n                font-weight: 700;\r\n                margin-bottom: 1rem;\r\n            }\r\n\r\n            input, textarea, select {\r\n                width: 100%;\r\n                border: 1px solid $divider-color;\r\n                outline: none;\r\n                font-size: 1.8rem;\r\n                border-radius: 3px;\r\n                &:focus {\r\n                    border-color: $primary-color;\r\n                }\r\n            }\r\n\r\n            input {\r\n                padding: 5px 10px;\r\n            }\r\n\r\n            textarea {\r\n                min-height: 20rem;\r\n                padding: 10px;\r\n            }\r\n\r\n            sel